      this.tableData.pageData.yhssb.sbbhead.sfzjhm=linq.from(this.initializationData.init.wsxx).where("item=>item.code=='SFZJHM'").defaultIfEmpty([]).toArray()[0].value;
        //
        this.tableData.pageData.otherData.zspmlist =  this.initializationData.init.zspm[0];
        this.tableData.pageData.otherData.yhssbGridlb[0].zspmDm = this.tableData.pageData.otherData.zspmlist.zsxmDm;
        this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[0].zspmDm = this.tableData.pageData.otherData.zspmlist.zsxmDm;
        //	-  -   
        this.tableData.pageData.otherData.yhssbGridlb[0].hdzsHdde = this.tableData.pageData.otherData.zspmlist.hdyj;
        this.tableData.pageData.otherData.yhssbGridlb[0].hdzsHdbl = this.tableData.pageData.otherData.zspmlist.hdbl;
        this.tableData.pageData.otherData.yhssbGridlb[0].sysl = this.tableData.pageData.otherData.zspmlist.sl;

        this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[0].hdzsHdde = this.tableData.pageData.otherData.zspmlist.hdyj;
        this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[0].hdzsHdbl = this.tableData.pageData.otherData.zspmlist.hdbl;
        this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[0].sysl = this.tableData.pageData.otherData.zspmlist.sl;
       // this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[0].sysl = this.tableData.pageData.otherData.zspmlist.hdbz;

        /*this.tableData.pageData.checkData.ZZS_QZD_HW = linq.from(this.initializationData.init.wsxx).where("item=>item.code=='ZZS_QZD_HW'").defaultIfEmpty([]).toArray()[0].value;*/
        this.tableData.pageData.yhssb.sbbhead.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh    //
        this.tableData.pageData.yhssb.sbbhead.nsrmc =this.initializationData.init.jbxx[0].nsrmc     //
        //
        // this.tableData.pageData.checkData.sfzsj = linq.from(this.initializationData.init.dmlb.dmb_sfzjlx).where("item=>item.mc=='ZZS_QZD_HW'").defaultIfEmpty([]).toArray()[0].value;
        //this.tableData.pageData.otherData.zlqxList = this.initializationData.init.dmlb.dmb_zlq
        this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate;  //

        //this.tableData.pageData.sbbhead.nsrsbh =  this.tableData.header.nsrsbh;  sksssjq: "20190401"
        //sksssjz: "20190430"
        this.tableData.pageData.yhssb.sbbhead.skssqq = this.initializationData.data[0].header.sksssjq;
        this.tableData.pageData.yhssb.sbbhead.skssqz = this.initializationData.data[0].header.sksssjz;

        this.tableData.pageData.checkData.hdbz = this.initializationData.init.zspm[0].hdbz;
        this.tableData.pageData.checkData.HDBL = this.initializationData.init.zspm[0].hdbl;
        this.tableData.pageData.checkData.HDYJ = this.initializationData.init.zspm[0].hdyj;

      },
      //
      betweenTables:function () {
        //   
        //this.tableData.pageData.checkData.wq=this.screeningReportData(this.calculatingDate(this.tableData.header.sksssjq,-1),this.calculatingDate(this.tableData.header.sksssjz,-1),this.tableData.header.bbDm,this.initializationData.data);
        /*//
        this.tableData.pageData.checkData.wq=this.screeningReportData(this.calculatingDate(this.tableData.header.sksssjq,-1),this.calculatingDate(this.tableData.header.sksssjz,-1),this.tableData.header.bbDm,this.initializationData.data);*/
      },
      //
      inTableVerification:function(){
				//12.
				this.validationValue.push({type:"fun",name:"flzlForm.jmse",function:this.jmsXybqynse,reportErrors:"",color:"red",comparisons:0});
				//3.90
				this.validationValue.push({type:"fun",function:this.dayuColumn9,name:"yhssb.yhssbGrid.yhssbGridlb.phjmse"});

        //this.validationValue.push({type:"fun",function:this.saveCheck,name:""});

			},
			//2.
			jmsXybqynse:function(){
				var yhssbGridlb = this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
				for(var i=0;i<yhssbGridlb.length;i++){
					var flag = true;
					if(yhssbGridlb[i].bqynse < yhssbGridlb[i].jmse){
						flag = false;
					}
					return flag;
				}
			},
      //90
      dayuColumn9:function(){
        var yhssbGridlb = this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
        for(var i=0;i<yhssbGridlb.length;i++){
          if(yhssbGridlb[i].phjmse<0){
            return `<div style="color:red" data-id="phjmse`+i+`" class="errorDiv">90</div><br>`
          }else{
            if(i==yhssbGridlb.length-1){
              return true;
            }
          }
        }
      },

      //1.50%0%
      isCheck:function(){
        if(this.tableData.pageData.yhssb.sbbhead.bqsfsyxgmyhzc=="Y"){
          return	this.tableData.pageData.yhssb.sbbhead.phjzbl="0.50";
        }else{
          return this.tableData.pageData.yhssb.sbbhead.phjzbl="0.00";
        }
      },

      /*13.009129907
      0
       009129906*/
      isSelectyyzb:function (){
        var  yhssbGridlb= this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
        for(var i=0;i<yhssbGridlb.length;i++){
          if(this.tableData.pageData.otherData.zspmlist.zspmMc==""&&(yhssbGridlb[i].jsje!="0"||yhssbGridlb[i].hdzsHdde!="0")){
            yhssbGridlb[i].phjmxzDm = "09129907";
            yhssbGridlb[i].phjmse = "0.00";
          }else if(this.tableData.pageData.otherData.zspmlist.zspmMc==""&&(yhssbGridlb[i].jsje!="0"||yhssbGridlb[i].hdzsHdde!="0")){
            yhssbGridlb[i].phjmxzDm = "09129906";
          }
          return this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[i];
        }
      },
      //bqsfsyxgmyhzcYSXA031900989
      isCheckycList(){
        if(this.tableData.pageData.yhssb.sbbhead.bqsfsyxgmyhzc=="Y"){
          this.tableData.pageData.yhssb.sbbhead.phjmxzDm="0009049901";
          this.tableData.pageData.yhssb.sbbhead.phjmswsxDm="SXA031900989";
          var yhssbGridlb =  this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
          for(var i=0;i<yhssbGridlb.length;i++){
            yhssbGridlb[i].phjmxzDm = "0009049901";
            yhssbGridlb[i].phjmswsxDm="SXA031900989";
          }
        }else{
          this.tableData.pageData.yhssb.sbbhead.phjmxzDm="";
          this.tableData.pageData.yhssb.sbbhead.phjmswsxDm="";
          var yhssbGridlb =  this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
          for(var i=0;i<yhssbGridlb.length;i++){
            yhssbGridlb[i].phjmxzDm = "";
            yhssbGridlb[i].phjmswsxDm="";
          }
        }
      },


      /*1.

 
*/
      /*saveCheck:function () {
        if (this.tableData.pageData.yhssb.sbbhead.nsrlx == "") {
          if (this.tableData.pageData.yhssb.sbbhead.bqsfsyxgmyhzc == "Y") {
            return `<div style="color:red" class="errorDiv"></div><br>`
            //_self.$elementUi.MessageBox.alert("json","",{type: 'warning'});
          } else {
            return;
          }
        }else if(this.tableData.pageData.yhssb.sbbhead.nsrlx == " "){
          if (this.tableData.pageData.yhssb.sbbhead.bqsfsyxgmyhzc == "Y") {
            return `<div style="color:red" class="errorDiv"></div><br>`
          } else {
            return;
          }
        }
      }*/

      /*******/
    },
    computed:{
      nsrlxdmmc(){
        var list = this.tableData.pageData.otherData.nsrlxdmMc;
        var listValue = this.tableData.pageData.otherData.nsrlxdmMcVal;
        for(var i=0;i<list.length;i++){
          if(listValue == list[i].dm){
            this.tableData.pageData.yhssb.sbbhead.nsrlx=list[i].dmmc;
            return this.tableData.pageData.yhssb.sbbhead.nsrlx;
          }
        }
      },
    	//2.5=1*4+2*3*4
     	column5:function(){
     	  return function(index){
     		  var  yhssbGridlb= this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
     		  var result ="";
          result = parseFloat(yhssbGridlb[index].jsje)*parseFloat(yhssbGridlb[index].sysl)+
		     	parseFloat(yhssbGridlb[index].hdzsHdbl)*parseFloat(yhssbGridlb[index].sysl)*parseFloat(yhssbGridlb[index].hdzsHdde);
          this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].bqynse=result.toFixed(2);
		     	return this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].bqynse;
        }
     	},
    	//4
    	column4:function(){

    	},
      //
      column2:function(){
     	  return function(index){
          var  yhssbGridlbs= this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].hdzsHdde;
          var yhssbGridlb = this.tableData.pageData.checkData.HDBL;
          var  result = "";
          if(this.tableData.pageData.checkData.hdbz==2){
            result = yhssbGridlbs;
          }else if(this.tableData.pageData.checkData.hdbz==1){
            result = yhssbGridlb;
            result = result.toFixed(2);
          }else{
            result = '0.00';
          }
          this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].hdzsHdde = result;
          return this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].hdzsHdde;
        }
      },
      //
      column3:function(){
        return function(index){
          var  yhssbGridlb= this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
          var  hdzsHdblIndex = this.tableData.pageData.checkData.HDBL;
          var  result = "";
          if(this.tableData.pageData.checkData.hdbz==2 ||this.tableData.pageData.checkData.hdbz==1){
            result = hdzsHdblIndex;
            result = result.toFixed(2);
          }else{
            result ='0.00';
          }
          this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].hdzsHdbl= result;
          return this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].hdzsHdbl;
        }
      },
      //6.95-8
      column9:function() {
        return function (index) {
          var yhssbGridlb = this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
          var result="";
          if(parseFloat(this.tableData.pageData.yhssb.sbbhead.phjzbl)==0||(parseFloat(yhssbGridlb[index].bqynse) - parseFloat(yhssbGridlb[index].jmse))==0){
            result = yhssbGridlb[index].phjmse;
          }else {
           result = (parseFloat(yhssbGridlb[index].bqynse) - parseFloat(yhssbGridlb[index].jmse)) * parseFloat(this.tableData.pageData.yhssb.sbbhead.phjzbl);
            result = result.toFixed(2);
          }
          this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].phjmse = result;
          return this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].phjmse;
        }
      },
      /*3.	10111010510
          5-6-8-90.1100 0.15-6-8-91101	5-6-8-9
          10111010510  5-6-8-90.11005-6-8-9*/
      column10c:function() {
        return function(index){
          var yhssbGridlb = this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
          var columnCon = parseFloat(yhssbGridlb[index].bqynse) - parseFloat(yhssbGridlb[index].bqyjse1) - parseFloat(yhssbGridlb[index].jmse) - parseFloat(yhssbGridlb[index].phjmse);
          var result="";
          if (this.tableData.pageData.yhssb.sbbhead.bqsfsyxgmyhzc == "Y") {
              if (yhssbGridlb[index].zspmDm == "101110105") {
                if (columnCon < 0.1) {
                  result= '0.00';
                } else if (columnCon < 1) {
                  result = '1.00';
                } else {
                  result = columnCon;
                  result = result.toFixed(2);
                }
              } else {
                if (columnCon < 0.1) {
                  result = '0.00';
                } else {
                  result = columnCon;
                  result = result.toFixed(2);
                }
              }
          }else{
            result = columnCon;
            result = result.toFixed(2);
          }
          this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].bqybtse = result;
          return this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb[index].bqybtse;
        }
      }


      /*******/
    },
    mounted: function () {
        this.keyboardSwitchingCell("taxTable","text");// input rbtbid;; text:
      //this.getPdf(this.name,true);
    },
    created:function () {
      this.initializationData = this.getSelectSbbInfo();
      this.initializationOfPublicServices();  //  header
      this.inTableVerification();
      this.$route.params.id;
      this.initializationDataAssignment();  //
      this.betweenTables();  //
	//	this.hdbzFun()  //1 2 3
      this.isCheckycList();
    }
  }
</script>

