
<template>
  <el-container>
    <el-header>
      <app-header :parentData="tableData" :parentValidation="validationValue" :parentPdfName="name"></app-header>
    </el-header>
    <el-container>
      <el-main id="allTable">
        <el-scrollbar style="height: 100%;">
          <div id="pdfDom" name="pdfDomBranch" class="pdfDomBranch">
            <table cellspacing="0" cellpadding="0" width="" height="238" id="taxTable">
              <tr>
                <td width="2511" height="46" align="center" valign="middle" colspan="13" class="noBorder">
                  <B></B>
                </td>
              </tr>
              <tr>
                <td width="2511" height="22" align="center" valign="middle" colspan="13" class="noBorder"><span v-format-date v-html="this.tableData.pageData.sbbhead.skssqq"></span>  <span v-format-date v-html="this.tableData.pageData.sbbhead.skssqz"></span></td>
              </tr>
              <tr>
                <td width="711" height="22" align="left" valign="middle" colspan="4" class="noBorder"><span v-html="this.tableData.pageData.sbbhead.nsrmc"></span></td>
                <td width="1800" height="22" align="left" valign="middle" colspan="9" class="noBorder"><span v-html="this.tableData.pageData.sbbhead.nsrsbh"></span></td>
              </tr>
              <tr>
                <td width="41" height="60" align="center" valign="middle" rowspan="2" class="uneditable-bgColor"></td>
                <td width="270" height="60" align="center" valign="middle" rowspan="2" class="uneditable-bgColor"></td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor"></td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor"></td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor"></td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">%</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor"></td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">/</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor"></td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">/</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">/</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor"></td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor"></td>
              </tr>
              <tr>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L1</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L2</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L3</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L4=L2/L3</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L5</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L6</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L7</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L8</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L9=L5*L6+L7*L8/L5+L7</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L10</td>
                <td width="200" height="30" align="center" valign="middle"  class="uneditable-bgColor">L11=L1/(1-L4)*L9*L10/(1+L10)</td>
              </tr>
              <tr  v-for="(item,index) in tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO">
                <td width="41" height="22" align="center" valign="middle"  v-html="item.ewbhxh"></td>                                  <!--  //-->
                <td width="270" height="22" align="center" valign="middle" class="editable-bgColor">
                  <input  type="text" v-model="item.cpmc1">
                </td>   <!--//-->
                <td width="200" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input @blur="retainValidDecimal('gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO['+index+'].dqxsncpsl','2')"  v-floatOld="2"  type="text" v-html="item.dqxsncpsl">
                </td>    <!--// L1-->
                <td width="200" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.shsl1" @blur="retainValidDecimal('gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO['+index+'].shsl1','2')">
                </td>  <!--//L2-->
                <td width="200" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.ncpgjsl"  @blur="retainValidDecimal('gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO['+index+'].ncpgjsl','2')">
                </td>  <!--//L3-->
                <td width="200" height="22" align="right" valign="middle" class="font-color-red" :text="L4Compute(index)" v-percent="4"  v-html="item.shl"></td>       <!--//%L4=L2/L3   V-PERCENT -->
                <td width="200" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.qckcncpsl"  @blur="retainValidDecimal('gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO['+index+'].qckcncpsl','2')">
                </td>    <!--//L5-->
                <td width="200" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.qcpjmj"  @blur="retainValidDecimal('gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO['+index+'].qcpjmj','2')">
                </td>   <!--///L6-->
                <td width="200" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.dqgjncpsl"  @blur="retainValidDecimal('gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO['+index+'].dqgjncpsl','2')">
                </td>   <!--/L7-->
                <td width="200" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.dqmj"  @blur="retainValidDecimal('gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO['+index+'].dqmj','2')">
                </td> <!--///L8-->
                <td width="200" height="22" align="right" valign="middle" class="font-color-red" :text="L9Compute(index)" v-html="item.ncppjgmdj"></td>   <!--/// L9=L5*L6+L7*L8/L5+L7-->
                <td width="1500" height="22" align="right" valign="middle" class="editable-bgColor">   <!--//L10-->
                  <select style="width: 50px" class="editable-bgColor select-bgcolor select-border" @change="selectVal(index)"  v-model="tableData.pageData.checkData.kclVal[index]">
                    <option :value="coupon" v-for="coupon in tableData.pageData.checkData.kclList" >{{coupon}}</option>
                  </select>
                </td>
                <td width="200" height="22" align="right" valign="middle" class="font-color-red"  :text="L11Compute(index)" v-html="item.dqyxdkncpjxse"></td>  <!--// L11=L1/(1-L4)*L9*L10/(1+L10)-->
                <td  width="150" height="22"  align="left" valign="middle" class="noBorder">
                  <addBtn :data-name="1" :data-num="index"  :parentListData="tableData" :parentListKey="'gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO'"v-show="index==tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO.length-1"></addBtn>
                  <deleteBtn :data-name="1" :data-num="index"  :parentListData="tableData" :parentListKey="'gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO'" v-show="index!=0"></deleteBtn>
                </td>
              </tr>
              <tr>
                <td width="1911" height="22" align="center" valign="middle" colspan="10"class="uneditable-bgColor"> </td>
                <td width="200" height="22" align="right" valign="middle" class="font-color-red"   v-html="tableData.pageData.otherData.trhj.ncppjgmdjhj"></td>
                <td width="200" height="22" align="center" valign="middle" class="uneditable-bgColor">--</td>
                <td width="200" height="22" align="right" valign="middle" class="font-color-red"  v-html="tableData.pageData.otherData.trhj.dqyxdkncpjxsehj"></td>
              </tr>
              <tr>
                <td width="2511" height="22" align="left" valign="middle" colspan="13" class="noBorder">1.</td>
              </tr>
              <tr>
                <td width="2511" height="22" align="left" valign="middle" colspan="13" class="noBorder">2.</td>
              </tr>
            </table>
          </div>
        </el-scrollbar>
      </el-main>
      <el-aside class="bar">
        <informationBar :parentName="name"></informationBar>
      </el-aside>
    </el-container>
  </el-container>
</template>

<script>
  /**
   *  @Author zhuli
   */
  import Header from "../../app-header";
  import informationBar from "../../informationBar.vue";
  import addBtn from '../../addBtn.vue'
  import deleteBtn from '../../deleteBtn.vue'
  import router from "../../../router/index.js";


  var linq = require("linq");
  export default {
    name: "10101_014_10",
    components: {
      "app-header": Header,
      informationBar: informationBar,
      'addBtn' : addBtn,
      'deleteBtn' : deleteBtn,
    },
    props: ["routerParam"],
    watch: { // 
      "tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO":{
        handler(newVal, oldVal){
          var _self = this;
          //
          _self.totalRows("gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO","otherData.trhj");
        },
        deep: true
      }
    },
    data: function () {
      return {
        certificateDictionary: "", //
        cityDictionary: "",
        validationValue: [], //
        requiredItems: [], //
        name: "10101_014_10", //
        initializationData: "", //
        saveInfo: "", //
        tableData: {
          header: {
            zsxmDm:"10101",
            bblxDm:"10101",
            bbDm:"10101_014",
            sksssjq:"20190401",
            sksssjz:"20190430",
            saveUserId:"949407",
            nsrsbq:"201905",
            multiPatch:"N",
            versionNumber:"11",
            nsrsbh:"91120102553420400T",
            djxh:"10111242000025815982",
            pcDm:0,
            nsqxdm:"06"
          },
          pageData: {
            gjncpzjxshdncpzzsjxseGrid:{  //gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO
              gjncpzjxshdncpzzsjxseGridlbVO:[
                {

                  xh:"",              //
                  ewbhxh:"1", //
                  cpmc1:"",        //
                  dqxsncpsl:"0.00",    //
                  shsl1:"0.00",        //
                  ncpgjsl:"0.00",      //
                  shl:"0.00",          //
                  qckcncpsl:"0.00",    //
                  qcpjmj:"0.00",       //
                  dqgjncpsl:"0.00",    //
                  dqmj:"0.00",         ///
                  ncppjgmdj:"0.00",    ///
                  kcl:"0.09",          //
                  dqyxdkncpjxse:"0.00" //
                }
              ]
            },
            sbbhead:{
              nsrsbh:"",   //
              nsrmc:"",    //
              skssqq:"",   //
              skssqz:""    //
            },
            // 
            checkData: {
              kclVal:{
                val1:"",
              },   //chejh42ckData.kclVal
              kclList:[]
            },
            otherData: {
              trhj:{
                ncppjgmdjhj:"0.00",
                dqyxdkncpjxsehj:"0.00"
              },
              /* */
              gjncpzjxshdncpzzsjxseGridlbVO:[
                {
                  ewbhxh: "1",   //	
                  xh:"",              //
                  cpmc1:"",        //
                  dqxsncpsl:"0.00",    //
                  shsl1:"0.00",        //
                  ncpgjsl:"0.00",      //
                  shl:"0.00%",          //
                  qckcncpsl:"0.00",    //
                  qcpjmj:"0.00",       //
                  dqgjncpsl:"0.00",    //
                  dqmj:"0.00",         ///
                  ncppjgmdj:"0.00",    ///
                  kcl:"0.09%",          //
                  dqyxdkncpjxse:"0.00" //
                }
              ]
            }

          },
        }
      };
    },
    computed: {

      //1. L4=L2/L3
      L4Compute(){
        return function (index) {
          var trGridlbVO = this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO[index];
          trGridlbVO.shl = parseFloat(trGridlbVO.ncpgjsl)==0? 0.00 : (parseFloat(trGridlbVO.shsl1)/parseFloat(trGridlbVO.ncpgjsl)).toFixed(2)
          return this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO[index].shl;
        }
      },
     // 2. L9=L5*L6+L7*L8/L5+L7
      L9Compute(){
        return function (index) {
          var trGridlbVO = this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO[index];
          trGridlbVO.ncppjgmdj =(parseFloat(trGridlbVO.qckcncpsl)+parseFloat(trGridlbVO.dqgjncpsl)) == 0?0.00: (parseFloat(trGridlbVO.qckcncpsl)*parseFloat(trGridlbVO.qcpjmj)+parseFloat(trGridlbVO.dqgjncpsl)*parseFloat(trGridlbVO.dqmj))/(parseFloat(trGridlbVO.qckcncpsl)+parseFloat(trGridlbVO.dqgjncpsl))
          return this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO[index].ncppjgmdj;
        }
      },
    // 3. L11=L1/(1-L4)*L9*L10/(1+L10)
      L11Compute(){
        return function (index) {
          var trGridlbVO = this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO[index];
          var result="";
          if(1-parseFloat(trGridlbVO.kcl)==0 || 1+parseFloat(trGridlbVO.dqyxdkncpjxse)==0){
            result="0.00";
          }else{
            result = parseFloat(trGridlbVO.dqxsncpsl)/(1-parseFloat(trGridlbVO.shsl1))*parseFloat(trGridlbVO.ncppjgmdj)*parseFloat(trGridlbVO.kcl)/(1+parseFloat(trGridlbVO.kcl))
            result = result.toFixed(2);
          }
          this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO[index].dqyxdkncpjxse= result;
          return this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO[index].dqyxdkncpjxse;
        }
      },
     // 4. L9=L9  
      //5. L11=L11  

    },




    created: function () {
      this.initializationData = this.getSelectSbbInfo();
      this.initializationOfPublicServices(); // 
      this.initializationDataAssignment();  //
      this.betweenTables();  //
      this.inTableVerification();//
      this.tableData.pageData.checkData.kclList=this.getDict(this.jsonUrl+"/static/json/dict/houseAdvantage");
      this.$route.params.id;
    },
    mounted: function () {
      this.$EventBus.$on("sendparent", ({sendpar}) => {
        console.log(sendpar);
        this.tableData.pageData.list = sendpar.list;
      });
      this.keyboardSwitchingCell("taxTable","text");// input rbtbid;; text:
    },
    methods: {

      //
      inTableVerification:function(){

        this.validationValue.push({type:"fun",name:"gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO",function:this.isNullcpmc}),
         this.validationValue.push({type:"fun",name:"gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO",function:this.L4Fun})
      },
      // 
      isNullcpmc: function(){
        var trccfhdncpzzsjxseGridlbVO = this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO;
        for (var i=0;i<trccfhdncpzzsjxseGridlbVO.length;i++){
          if(trccfhdncpzzsjxseGridlbVO[i].cpmc1==''|| trccfhdncpzzsjxseGridlbVO[i].cpmc1==null){
            return `<div style="color:red"  data-id="cpmc1`+i+`" class="errorDiv"></div><br>`
          }else{
            if(i==trccfhdncpzzsjxseGridlbVO.length-1){
              return true;
            }
          }
        }
      },


      // 0L41
      L4Fun: function(){
        var trccfhdncpzzsjxseGridlbVO = this.tableData.pageData.gjncpzjxshdncpzzsjxseGrid.gjncpzjxshdncpzzsjxseGridlbVO;
        for(var i=0;i<trccfhdncpzzsjxseGridlbVO.length;i++){
          var shl = trccfhdncpzzsjxseGridlbVO[i].shl;
          if(shl>1 ||shl<0 ){
            return `<div style="color:red" class="errorDiv">0L81</div><br>`
          }else{
            return true;
          }
        }
      },

      //select
      selectVal:function(index){
        var kclVal = this.tableData.pageData.checkData.kclVal[index] ;
        console.log(kclVal);
      },
      initializationDataAssignment:function () {
        // console.log(this.initializationData)
        //
        // this.tableData.pageData.checkData.zfjgdm = linq.from(this.initializationData.init.wsxx).where("item=>item.code=='hdxx'").defaultIfEmpty([]).toArray()[0].value;
        // console.log(this.tableData.pageData.checkData.zfjgdm)
        //
         this.tableData.pageData.sbbhead.nsrmc =this.initializationData.init.jbxx[0].nsrmc     //
        this.tableData.pageData.sbbhead.nsrsbh =this.initializationData.init.jbxx[0].nsrsbh   //

        // // this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate  //
         this.tableData.pageData.sbbhead.skssqq =  this.tableData.header.sksssjq
         this.tableData.pageData.sbbhead.skssqz =  this.tableData.header.sksssjz

      },
    },
    //
    provide(){
      return{
        resetData:this.resetData
      }
    },
  };
</script>

<style scoped>
  .selectkcl{
    width: 100%;
  }
</style>





