      //2_34
      hjhjFun:function(){
        var hjhj = this.tableData.pageData.otherData.tynsyhbxxtqyzGridlbVOhj.hjhj;
        var zbList = this.flzlbsj1.pageData.zzsxgmGrid.zzsxgmGridlb;
        var zbListHj = parseFloat(zbList[0].bqybtse)+parseFloat(zbList[2].bqybtse);
        if(parseFloat(hjhj)!= parseFloat(zbListHj)){
