        var msAllTotal = parseFloat(mzzzsxmxsehj) + parseFloat(zzsjmssbmxbmsxmGridlbVOhj.bqsjkcjehj) + parseFloat(zzsjmssbmxbmsxmGridlbVOhj.kchmsxsehj) + parseFloat(zzsjmssbmxbmsxmGridlbVOhj.msehj);
        this.tableData.pageData.otherData.msAllTotal = msAllTotal;
      },
