<template>
  <!--<el-button type="primary" @click="saveData(tableData)"></el-button>-->
    <el-row  :gutter="20">
      <el-col :span="16">
        <el-button type="success" size="medium" icon="el-icon-back" @click="returnHomePage" plain v-show="allParam.returnBtn"></el-button>
        <el-button type="success" size="medium" icon="el-icon-upload" @click="getOtherDataZ" plain v-show="allParam.getOtherData"></el-button>
        <el-button type="primary" size="medium" icon="el-icon-printer" @click="getPdf(pdfName)" v-show="allParam.pdfBtn" plain>pdf</el-button>
        <el-button type="primary" size="medium" icon="el-icon-printer" @click="getPdfBranch(pdfName)" v-show="allParam.returnBtn" plain></el-button>
        <el-button type="primary" size="medium" icon="el-icon-printer" @click="printData()" v-show="allParam.printBtn" plain></el-button>
        <el-button type="primary" size="medium"icon="el-icon-upload" @click="saveData(tableData)" v-show="allParam.saveBtn" plain></el-button>
        <el-button type="primary" size="medium"icon="el-icon-warning" @click="resetDataParent()" v-show="allParam.replaceBtn" plain></el-button>
        <el-button type="danger" size="medium" icon="el-icon-delete" @click="deleteData" v-show="allParam.deleteBtn" plain></el-button>
        <el-button type="danger" size="medium" icon="el-icon-delete" @click="dataToTimestamp('20190228','-1')" plain></el-button>
      </el-col>
      <el-col :span="5"><el-button type="text" @click="openBar()"></el-button></el-col>
      <el-col style="text-align: right" :span="3" ><el-button @click="closeBar()" type="text"></el-button></el-col>
    </el-row>
</template>

<script>
  import bus from '../../static/js/eventBus.js'
  import taxTable from '../../static/css/taxTable.css'
  import Vue from 'vue'
  var linq = require('linq');
    export default {
      name: "app-header",
      inject:['resetData','getOtherData'],
      props: ['parentData','parentValidation','parentPdfName','parentRequiredItems','parentCheckData','parentResetData'],
      data: function () {
        return {
          tableData: this.parentData,
          validationValue: this.parentValidation,
          pdfName:this.parentPdfName,
          requiredItems:this.parentRequiredItems,
          checkData:this.parentCheckData,
          resetData:this.parentResetData,
          regD:RegExp(/checkData\./g), // @
          allParam:{
            saveBtn:true,
           /* getOtherData:true,*/
            returnBtn:true
          }
        }
      },
      methods:{
        //
        resetDataParent:function(){
          this.tableData = this.resetData;

          /*for(var key in this.tableData.pageData){
            if(key == "list"){
              for(var key1 in this.tableData.pageData[key]){
                for(var j=0;j<this.tableData.pageData.list[key1].length;j++){
                  for(var key2 in this.tableData.pageData.list[key1][j]){
                    this.tableData.pageData.list[key1][j][key2] = "";
                  }
                }
              }
            }else{
              this.tableData.pageData[key]=""
            }
          }*/
        //  this.resetData();
        },
        /**
         * @Author ygy
         * @Description //TODO 
         * @Date 17:07 2019/8/22
         **/
        getOtherDataZ:function(){
          this.getOtherData()
        },
        //
        returnHomePage:function () {
          /*this.$router.go(-1);//*/
          window.history.back(-1);
        },
        //
        saveData : function(param){
         // this.$elementUi.MessageBox.alert("","")
          console.log("             ")
          console.log(param);
          //this.$elementUi.MessageBox.alert("","",{type: 'warning'});
          if(this.validationAll()==0){
            this.$https.post('/htftsolu/saveSbbInfo',{tableData:JSON.stringify(param)} ).then((data) => {
            }).catch(err=>{
                console.log(err)
              }
            )
          }
        },
        //
        openBar:function(){
          $(".bar").show();
          this.$EventBus.$emit("activeName", {
            activeName:"first",
          });
          this.tool.adjustTableSize("openBar");
        },
        //
        closeBar:function(){
          $(".bar").hide();
          this.tool.adjustTableSize("closeBar");
        },
        //
        extractionOfData:function () {
          alert("Extraction of data");
        },
        //
        deleteData:function () {
          alert("delete");
        },
        //
        printData:function () {
          $("#pdfDom").jqprint({
            debug: false, //iframe
            importCSS: true,
            printContainer: true,
            operaSupport: false,
            staticUrl:this.jsonUrl
          });
        },
        strToFun:function(str,type){

          var _self = this;
          var json = new Function("_self","return _self.tableData.pageData." + str);
          if(type=="string"){
            return json(_self);
          }else{
            return parseFloat(json(_self));
          }
        },
        //
        validationAll:function () {
          $("#taxTable input").removeClass("error-input");
          $("#taxTable select").removeClass("error-input");
          $("#taxTable td").removeClass("error-input");
          var flag1 = 0;
          $("#ces").html("");
          var str = "<div>";
          var _self = this;
          console.log(_self.validationValue)
          $.each(_self.validationValue,function (index,item) {
            var names = item.name.split(".");
            var name = names[names.length-1];
            if(item.location){
              name = item.location;
            }
            console.log(name)
            var data = _self.strToFun(item.name,"string");
            if(item.type == "phone"&&(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(data))||!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(data))){
              str+=`<div style="color:`+item.color+`" data-id="`+name+`" class="errorDiv">`+item.reportErrors+`</div><br>`;
              flag1 += item.color=="green"?0:1;
            }
            if(item.type == "null"&&!data){
              str+=`<div style="color:`+item.color+`" data-id="`+name+`" class="errorDiv">`+item.reportErrors+`</div><br>`;
              flag1 += item.color=="green"?0:1;
            }
            // 
            if(item.type == "absolute"&&Math.abs(_self.strToFun(item.name))>item.comparisons){
              str+=`<div style="color:`+item.color+`" data-id="`+name+`" class="errorDiv">`+item.reportErrors+`</div><br>`;
              flag1 += item.color=="green"?0:1;
            }
            //
            if(item.type == ">="){
                var arr = item.name.split(",");

              if(arr.length>1){
                var arr1 = item.index.split(",");
                $.each(arr,function (index,i) {
                  if (_self.strToFun(i)<item.comparisons){
                      var errorLog = ""+arr1[index]+item.reportErrors;
                    str+=`<div style="color:`+item.color+`" data-id="`+name+`"  class="errorDiv">`+errorLog+`</div><br>`;
                    flag1 += item.color=="green"?0:1;
                  }
                })
              }else{
                if (_self.strToFun(item.name)<item.comparisons){
                  str+=`<div style="color:`+item.color+`" data-id="`+name+`"  class="errorDiv">`+item.reportErrors+`</div><br>`;
                  flag1 += item.color=="green"?0:1;
                }
              }
            }
            // 
            if(item.type == "=" && _self.strToFun(item.name)!= _self.strToFun(item.name1)){
              str+=`<div style="color:`+item.color+`" data-id="`+name+`"  class="errorDiv">`+item.reportErrors+`</div><br>`;
              flag1 += item.color=="green"?0:1;
            }
            // 
            if(item.type == ">" && _self.strToFun(item.name)<= item.comparisons){
              str+=`<div style="color:`+item.color+`" data-id="`+name+`" class="errorDiv">`+item.reportErrors+`</div><br>`;
              flag1 += item.color=="green"?0:1;
            }
            // 
            if(item.type == "fun"){
              var flag = item.function();
              if(!flag){
                str+=`<div style="color:`+item.color+`" data-id="`+name+`" class="errorDiv">`+item.reportErrors+`</div><br>`;
                flag1 += item.color=="green"?0:1;
              }else if(typeof (flag)=="string"){
                str+=flag+``;
                flag1 += item.color=="green"?0:1;
              }
            }
          })
          if(str.length>0){
            $(".bar").show();
          }
          this.$EventBus.$emit("activeName", {
            activeName:"second",
          });
          str+="</div>"
          $("#ces").html(str)
          // 
          $(".errorDiv").on('click',function (e) {
            $("#taxTable input").removeClass("error-input");
            $("#taxTable select").removeClass("error-input");
            $("#taxTable td").removeClass("error-input");
            if($("#taxTable [data-id='"+e.target.dataset.id+"']").length>0){
              $("#taxTable [data-id='"+e.target.dataset.id+"']").addClass("error-input");
              if($("#taxTable [data-id='"+e.target.dataset.id+"']")&&$("#taxTable [data-id='"+e.target.dataset.id+"']")[0].localName != "td"){
                $("#taxTable [data-id='"+e.target.dataset.id+"']").parent().addClass("error-input");
              }
            }else{
              $("#taxTable td[data-id='"+e.target.dataset.id+"']").addClass("error-input");
            }
          })
          return flag1;
        },
      },
    }
</script>

<style scoped>
.padding{
  padding: 12px;
  background-color: #E4E7ED;
  border-radius: 6px;
  right: 2px;
}
  .backgroundColorYellow{
    background-color: yellow;
  }
</style>



