        var length = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO.length;
        var lc = parseFloat(length) + parseFloat(num);
        this.tableData.pageData.otherData[key] = lc.toString();
        return this.tableData.pageData.otherData[key];
      }
    },
    //   3=1+2 bqydjse=qcye+bqfse
    jsxmColumn3() {
      return function (index) {
        var qcye = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].qcye;
        var bqfse = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqfse;
        var result = parseFloat(qcye) + parseFloat(bqfse);
        this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqydjse = result.toFixed(2);
        return this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqydjse;
      };
    },
    //   5=3-4 qmye=bqydjse-bqsjdjse
    jsxmColumn5() {
      return function (index) {
        var bqydjse = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqydjse;
        var bqsjdjse = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqsjdjse;

        var result = parseFloat(bqydjse) - parseFloat(bqsjdjse);
        this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].qmye = result.toFixed(2);
        return this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].qmye;
      };
    },

    //   3=1-2 kchmsxse=mzzzsxmxse-bqsjkcje
    msxmColumn3() {
      return function (index) {
        var mzzzsxmxse = this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[index].mzzzsxmxse;
        var bqsjkcje = this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[index].bqsjkcje;

        var result = parseFloat(mzzzsxmxse) - parseFloat(bqsjkcje);
        this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[index].kchmsxse = result.toFixed(2);
        return this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[index].kchmsxse;
      };
    },
  },
  watch: {
    // 
    // 
    'tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO': {
      handler(newVal, oldVal) {
        var _self = this;
        _self.totalRows(
          'zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO',
          'otherData.zzsjmssbmxbjsxmGridlbVOhj'
        );
        this.changeJSXM = true;
      },
      deep: true
    },
    // 
    'tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO': {
      handler(newVal, oldVal) {
        var _self = this;
        _self.totalRows(
          'zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO',
          'otherData.zzsjmssbmxbmsxmGridlbVOhj'
        );
        this.changeMSXM = true;
      },
      deep: true
    },
    //  
    'tableData.pageData.otherData.zzsjmssbmxbmsxmGridlbVOhj': {
      handler(newVal, oldVal) {
        var zzsjmssbmxbmsxmGridlbVOhj = this.tableData.pageData.otherData.zzsjmssbmxbmsxmGridlbVOhj;
        var msAllTotal = parseFloat(zzsjmssbmxbmsxmGridlbVOhj.mzzzsxmxsehj) + parseFloat(zzsjmssbmxbmsxmGridlbVOhj.bqsjkcjehj) + parseFloat(zzsjmssbmxbmsxmGridlbVOhj.kchmsxsehj) + parseFloat(zzsjmssbmxbmsxmGridlbVOhj.msehj);
        this.tableData.pageData.otherData.msAllTotal = msAllTotal;
      },
      deep: true
    }
  },
  created: function () {
    this.initializationData = this.getSelectSbbInfo(); //  
    this.initializationDataAssignment(); //  
    this.inTableVerification(); // 
    this.betweenTables(); // 
    this.initializationOfPublicServices()  //  header
  },
  mounted: function () {
    this.keyboardSwitchingCell('taxTable', 'text'); // input rbtbid;; text:
    //this.getPdf(this.name,true);
  },
  methods: {
    // 
    initializationDataAssignment: function () {
      this.tableData.pageData.otherData.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh;//
      this.tableData.pageData.otherData.nsrmc = this.initializationData.init.jbxx[0].nsrmc;//
      this.tableData.pageData.otherData.skssqq = this.initializationData.data[0].header.sksssjq;//
      this.tableData.pageData.otherData.skssqz = this.initializationData.data[0].header.sksssjz;//
      this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate  //
      // 
      this.modalJSXM.tableColumn = linq.from(this.initializationData.init.dmxx).where("item=>item.jmxz=='jsxx'").defaultIfEmpty([]).toArray();//
      this.jsxmDM = linq.from(this.initializationData.init.dmxx).where("item=>item.jmxz=='jsxx'").defaultIfEmpty([]).toArray();//
      // 
      this.modalMSXM.tableColumn = linq.from(this.initializationData.init.dmxx).where("item=>item.jmxz=='msxx'").defaultIfEmpty([]).toArray();//
      this.msxmDM = linq.from(this.initializationData.init.dmxx).where("item=>item.jmxz=='jsxx'").defaultIfEmpty([]).toArray();//

    },
    // 
    betweenTables: function () {

    },
    //
    inTableVerification: function () {
      // // 1
      this.validationValue.push({ type: 'fun', name: 'hmc', function: this.repeatJsxz });
      // 2
      this.validationValue.push({ type: 'fun', name: 'hmc', function: this.isNullJsxz });
      // 3
      this.validationValue.push({ type: 'fun', name: 'hmc', function: this.repeatJmxz });
      // 4
      this.validationValue.push({ type: 'fun', name: 'hmc', function: this.isNullJmxz });
      // 5-4<=-3
      this.validationValue.push({ type: 'fun', name: 'bqsjdjse', function: this.jsCol4 });
      // 6-5>=0
      this.validationValue.push({ type: 'fun', name: 'mse', function: this.msCol5 });
      // 7-4>=0
      this.validationValue.push({ type: 'fun', name: 'bqsjdjse', function: this.jsCol4Large0 });
    },
    // 1
    repeatJsxz: function () {
      if(this.changeJSXM){
        var zzsjmssbmxbjsxmGridlbVO = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO;
        var result = [];
        for (var i = 0; i < zzsjmssbmxbjsxmGridlbVO.length; i++) {
          //i
          //push
          //indexOf result-1
          if (zzsjmssbmxbjsxmGridlbVO[i].hmc != '' || zzsjmssbmxbjsxmGridlbVO[i].hmc != null) {
            if (result.indexOf(zzsjmssbmxbjsxmGridlbVO[i].hmc) < 0) {
              result.push(zzsjmssbmxbjsxmGridlbVO[i].hmc);
              if (i == zzsjmssbmxbjsxmGridlbVO.length - 1) {
                return true;
              }
            } else {
              return `<div style="color:red" data-id="hmc` + i + `js" class="errorDiv"></div><br>`;;
            }
          } else {
            return true;
          }
        }
      }
    },
    // 2
    isNullJsxz: function () {
      if(this.changeJSXM){
      var zzsjmssbmxbjsxmGridlbVO = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO;
      for (var i = 0; i < zzsjmssbmxbjsxmGridlbVO.length; i++) {
        if (zzsjmssbmxbjsxmGridlbVO[i].hmc == '' || zzsjmssbmxbjsxmGridlbVO[i].hmc == null) {
          return `<div style="color:red" data-id="hmc` + i + `js" class="errorDiv"></div><br>`;
        } else {
          if (i == zzsjmssbmxbjsxmGridlbVO.length - 1) {
            return true;
          }
        }
      }
      }
    },
    // 3
    repeatJmxz: function () {
      if(this.changeMSXM){
      var zzsjmssbmxbmsxmGridlbVO = this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO;
      var result = [];
      for (var i = 0; i < zzsjmssbmxbmsxmGridlbVO.length; i++) {
        //i
        //push
        //indexOf result-1
        if (zzsjmssbmxbmsxmGridlbVO[i].hmc != '' || zzsjmssbmxbmsxmGridlbVO[i].hmc != null) {
          if (result.indexOf(zzsjmssbmxbmsxmGridlbVO[i].hmc) < 0) {
            result.push(zzsjmssbmxbmsxmGridlbVO[i].hmc);
            if (i == zzsjmssbmxbmsxmGridlbVO.length - 1) {
              return true;
            }
          } else {
            return `<div style="color:red" data-id="hmc` + i + `ms" class="errorDiv"></div><br>`;
          }
        } else {
          return true;
        }
      }}
    },
    // 4
    isNullJmxz: function () {
      if(this.changeMSXM){
      var zzsjmssbmxbmsxmGridlbVO = this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO;
      for (var i = 0; i < zzsjmssbmxbmsxmGridlbVO.length; i++) {
        if (zzsjmssbmxbmsxmGridlbVO[i].hmc == '' || zzsjmssbmxbmsxmGridlbVO[i].hmc == null) {
          return `<div style="color:red" data-id="hmc` + i + `ms" class="errorDiv"></div><br>`;
        } else {
          if (i == zzsjmssbmxbmsxmGridlbVO.length - 1) {
            return true;
          }
        }
      }}
    },
    // 5-4<=-3
    jsCol4: function () {
      // 
      var zzsjmssbmxbjsxmGridlbVO = this.tableData.pageData.zzsjmssbmxbjsxmGrid
        .zzsjmssbmxbjsxmGridlbVO;
      var result = true;
      for (var item of zzsjmssbmxbjsxmGridlbVO) {
        if (item.bqsjdjse <= item.bqydjse) {
          result = true;
        } else {
          result = `<div style="color:red" class="errorDiv">-4<=-3</div><br>`;
        }
      }
      // 
      var zzsjmssbmxbjsxmGridlbVOhj = this.tableData.pageData.otherData.zzsjmssbmxbjsxmGridlbVOhj;
      if (zzsjmssbmxbjsxmGridlbVOhj.bqsjdjsehj <= zzsjmssbmxbjsxmGridlbVOhj.bqydjsehj) {
        result = true;
      } else {
        result = `<div style="color:red" class="errorDiv">-4<=-3</div><br>`;
      }
      return result;
    },
    // 6-5>=0
    msCol5: function () {
      // 
      var zzsjmssbmxbmsxmGridlbVO = this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO;
      var result = true;
      for (var item of zzsjmssbmxbmsxmGridlbVO) {
        if (item.mse >= 0) {
          result = true;
        } else {
          result = `<div style="color:red" class="errorDiv">-5>=0</div><br>`;
        }
      }
      // 
      var zzsjmssbmxbmsxmGridlbVOhj = this.tableData.pageData
        .otherData.zzsjmssbmxbmsxmGridlbVOhj;
      if (zzsjmssbmxbmsxmGridlbVOhj.msehj >= 0) {
        result = true;
      } else {
        result = `<div style="color:red" class="errorDiv">-5>=0</div><br>`;
      }
      return result;
    },
    // 7-4>=0
    jsCol4Large0: function () {
      // 
      var zzsjmssbmxbjsxmGridlbVO = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO;
      var result = true;
      for (var item of zzsjmssbmxbjsxmGridlbVO) {
        if (item.bqsjdjse >= 0) {
          result = true;
        } else {
          result = `<div style="color:red" class="errorDiv">-4>=0</div><br>`;
        }
      }
      // 
      var zzsjmssbmxbjsxmGridlbVOhj = this.tableData.pageData
        .otherData.zzsjmssbmxbjsxmGridlbVOhj;
      if (zzsjmssbmxbjsxmGridlbVOhj.bqsjdjsehj >= 0) {
        result = true;
      } else {
        result = `<div style="color:red" class="errorDiv">-4>=0</div><br>`;
      }
      return result;
    },
    //  
    showDialog: function (type, index) {
      //  
      if (type == "JSXM") {
        this.modalJSXM.isShow = true;
        this.jsxmIndex = index;
        console.log('length', this.modalMSXM.tableColumn.length);
      }
      //  
      if (type == "MSXM") {
        this.modalMSXM.isShow = true;
        this.msxmIndex = index;
      }
    },
    //   
    changeTableJSXM: function (val) {
      this.checkedJSXM = val;
      this.getCurrentRow(val);
      // this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[this.jsxmIndex].hmc=val.dm+' '+val.mc;
      // this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[this.jsxmIndex].swsxDm=val.swsxDm;
    },
    //   
    changeTableMSXM: function (val) {
      this.checkedMSXM = val;
      this.getCurrentRow(val);
      // this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[this.msxmIndex].hmc=val.dm+' '+val.mc;
      // this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[this.msxmIndex].swsxDm=val.swsxDm;
    },

    //  
    dialogClose: function () {
      this.dialogJSXM = false;
      this.dialogMSXM = false;
    },
    cancelChoose: function (type) {
      if (type == "JSXM") {
        this.checkedJSXM = '';
        this.$refs.jsxmTable.setCurrentRow();
        this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[this.jsxmIndex].hmc = '';
        this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[this.jsxmIndex].swsxDm = '';
      }
      if (type == "MSXM") {
        this.checkedMSXM = '';
        this.$refs.msxmTable.setCurrentRow();
        this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[this.msxmIndex].hmc = '';
        this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[this.msxmIndex].swsxDm = '';
      }
    },
    getCurrentRow: function (val) {
      console.log('val', val);
    },
    sureChoose: function (type) {
      if (type == "JSXM") {
        console.log('this.checkedJSXM', this.checkedJSXM);
        if (this.checkedJSXM != '' || this.checkedJSXM != null) {
          this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[this.jsxmIndex].hmc = this.checkedJSXM.dm + ' ' + this.checkedJSXM.mc;
          this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[this.jsxmIndex].swsxDm = this.checkedJSXM.swsxDm;
        }
        this.modalJSXM.isShow = false;
        this.jsxmIndex = '';
        // this.checkedJSXM = '';
      }
      if (type == "MSXM") {
        if (this.checkedMSXM != '' || this.checkedMSXM != null) {
          this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[this.msxmIndex].hmc = this.checkedMSXM.dm + ' ' + this.checkedMSXM.mc;
          this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[this.msxmIndex].swsxDm = this.checkedMSXM.swsxDm;
        }
        this.modalMSXM.isShow = false;
        this.msxmIndex = '';
        // this.checkedMSXM = '';
      }
    }
  },
  provide() {
    //
    return {
      resetData: this.resetData
    };
  }
};
</script>

<style scoped>
.tdBorder th {
  border: 1px solid #333 !important;
}
.cancelBtn {
  margin-top: 20px;
}
.el-table__body{
  width: 100%
}
</style>




