    console.log('this.initializationData.data[0].sksssjq',this.initializationData.data[0].sksssjq)
      this.tableData.pageData.bfcpxstjbForm.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh; //
      this.tableData.pageData.bfcpxstjbForm.nsrmc = this.initializationData.init.jbxx[0].nsrmc; //
      this.tableData.pageData.bfcpxstjbForm.skssqq = this.initializationData.data[0].header.sksssjq; //
      this.tableData.pageData.bfcpxstjbForm.skssqz = this.initializationData.data[0].header.sksssjz; //
      this.tableData.pageData.bfcpxstjbForm.tbrq = this.initializationData.openTableData.openTableDate  //
      
      //  HYMX_DM
      this.tableData.pageData.otherData.hymxdm = linq.from(this.initializationData.init.wsxx).where("item=>item.code=='HYMX_DM'").defaultIfEmpty([]).toArray()[0].value;
      console.log('HYMX_DM',this.tableData.pageData.otherData.hymxdm);
    },
    // 
    betweenTables: function () { },
    //
    inTableVerification: function () { }
  },
  provide() {
    return {
      resetData: this.resetData
    };
  }
};
</script>

