        return function (index) {
          var trGridlbVO = this.tableData.pageData.trccfhdncpzzsjxseGrid.trccfhdncpzzsjxseGridlbVO[index];
          var result="";
          if((parseFloat(trGridlbVO.qckcncpsl)+parseFloat(trGridlbVO.dqgjncpsl))==0){
            result='0.00';
          }else{
            result = (parseFloat(trGridlbVO.qckcncpsl)*parseFloat(trGridlbVO.qcpjmj)+parseFloat(trGridlbVO.dqgjncpsl)*parseFloat(trGridlbVO.dqmj))/(parseFloat(trGridlbVO.qckcncpsl)+parseFloat(trGridlbVO.dqgjncpsl));
           // trGridlbVO.pjgmdj= trGridlbVO.pjgmdj.toFixed(2);
