          }
          this.tableData.pageData.trccfhdncpzzsjxseGrid.trccfhdncpzzsjxseGridlbVO[index].pjgmdj = result;
          return this.tableData.pageData.trccfhdncpzzsjxseGrid.trccfhdncpzzsjxseGridlbVO[index].pjgmdj;
        }
