        var trccfhdncpzzsjxseGridlbVO = this.tableData.pageData.trccfhdncpzzsjxseGrid.trccfhdncpzzsjxseGridlbVO;
        for(var i=0;i<trccfhdncpzzsjxseGridlbVO.length;i++){
          var cpmc1 = trccfhdncpzzsjxseGridlbVO[i].cpmc1;
          if(cpmc1==""||cpmc1==null){
            return `<div style="color:red" data-id="cpmc1`+i+`" class="errorDiv"></div><br>`
          }else{
