    this.initializationOfPublicServices()  //  header
  },
  mounted: function () {
    this.keyboardSwitchingCell("taxTable", "text"); // input rbtbid;; text:
    //this.getPdf(this.name,true);
  },
  methods: {
    // 
    initializationDataAssignment: function () {
      this.tableData.pageData.sbbheadVO.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh    //
      this.tableData.pageData.sbbheadVO.nsrmc = this.initializationData.init.jbxx[0].nsrmc     //
      this.tableData.pageData.sbbheadVO.skssqq = this.tableData.header.sksssjq//
      this.tableData.pageData.sbbheadVO.skssqz = this.tableData.header.sksssjz//
      this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate  //

    },
    // 
    betweenTables: function () {
      this.tableData.pageData.otherData.ypxhOption = this.initializationData.init.dmlb.dmb_ypxh;
    },
    //
    inTableVerification: function () {
      this.validationValue.push({
        type: 'fun',
        name: 'ypxh',
        function: this.isNullJsxz
      });
    },
    isNullJsxz: function () {
      var cpygxcqkmxbGridlbVO = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO;
      if (this.isNull(cpygxcqkmxbGridlbVO, 'ypxh') != false) {
        return true;
      } else {
        return `<div style="color:red" class="errorDiv"></div><br>`;
      }
    },
    // 
    isNull: function (arr, key) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i][key] == '' || arr[i][key] == null) {
          return false;
        }
      }
    },
  },
  provide() {
    return {
      resetData: this.resetData
    };
  }
};
</script>

