    jzzcDate: function () {
      var syxgmjzzcqssj = this.tableData.pageData.nsrxxForm.syxgmjzzcqssj;
      var syxgmjzzczzsj = this.tableData.pageData.nsrxxForm.syxgmjzzczzsj;
      if ((syxgmjzzcqssj != "" || syxgmjzzcqssj != null) && (syxgmjzzczzsj != "" || syxgmjzzczzsj != null)) {
        var startDay = this.dataToTimestamp(syxgmjzzcqssj);
        var endDay = this.dataToTimestamp(syxgmjzzczzsj);
        if (startDay > endDay) {
          this.$elementUi.MessageBox.alert(
            "",
            "",
            { type: "info" }
          );
        }
      }
    },
    monthInterval: function (date1, date2) {
      // 
      date1 = date1.split('-');
      // 
      date1 = parseInt(date1[0]) * 12 + parseInt(date1[1]);
      // 
      date2 = date2.split('-');
      // 
      date2 = parseInt(date2[0]) * 12 + parseInt(date2[1]);
      var monthNum = Math.abs(date1 - date2);
      return monthNum;
    },
    dialogShow: function (type){
      this.loading = true;
      if(type=="DJZCLX"){
        this.dialogDJZCLX = true;// 
      }
      if(type=="SSHY"){
        this.dialogSSHY = true;// 
      }
    },
    changeTableDJZCLX: function (val){
      this.checkedDJZCLX = val;
      this.getCurrentRow(val);
    },
    changeTableSSHY: function (val){
      this.checkedSSHY = val;
      this.getCurrentRow(val);
    },
    getCurrentRow: function (val){
      console.log('val',val);
    },
    sureChoose: function (type){
      if(type=="DJZCLX"){
        this.tableData.pageData.nsrxxForm.djzclxDm = this.checkedDJZCLX.dm + ' '+this.checkedDJZCLX.mc;
        this.dialogDJZCLX = false;// 
      }
      if(type=="SSHY"){
        console.log('this.checkedSSHY',this.checkedSSHY);
        this.tableData.pageData.nsrxxForm.sshyDm = this.checkedSSHY.dm + ' '+this.checkedSSHY.mc;
        this.dialogSSHY = false;// 
      }
    },
    cancelChoose: function (type){
      if(type=="DJZCLX"){
        this.checkedDJZCLX = '';
        this.$refs.djzclxTable.setCurrentRow();
      }
      if(type=="SSHY"){
        this.checkedSSHY = '';
        this.$refs.sshyTable.setCurrentRow();
      }
    },
    cancelDJZCLX: function (rows){
      this.checkedDJZCLX = '';
      this.$refs.djzclxTable.setCurrentRow();
    },
    cancelSSHY: function (rows){
      this.checkedSSHY = '';
      this.$refs.sshyTable.setCurrentRow();
    },
    toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
    dialogClose: function (){
      this.dialogDJZCLX = false;
      this.dialogSSHY = false;
      this.$refs.djzclxTable.setCurrentRow();
      this.$refs.sshyTable.setCurrentRow();
    }
