          }else{
            result = (parseFloat(trGridlbVO.dqhyncpsl)*parseFloat(trGridlbVO.ncppjgmdj)*parseFloat(trGridlbVO.kcl))/(1+0.09);
            result = result.toFixed(2);
          }
