          var avgZcze = ( parseFloat(this.tableData.pageData.jczcze)+ parseFloat(this.tableData.pageData.jmzcze))/2
          var sjlre = parseFloat(this.tableData.pageData.sjlre)
          if(this.tableData.pageData.qylx =="3"){
            this.tableData.pageData.xxwlqy = "N";
          }else if(this.tableData.pageData.gjxzhjzhy == "N"&&avgCyrs<=300&&avgZcze<=5000&&sjlre<=3000000){
              this.tableData.pageData.xxwlqy = "Y";
          }else{
            this.tableData.pageData.xxwlqy = "N";
          }
          return this.tableData.pageData.xxwlqy
