            this.tableData.pageData.jmbyqndks = parseFloat(this.tableData.pageData.lrze)+parseFloat(this.tableData.pageData.jtdywjsdynsdse)-parseFloat(this.tableData.pageData.jbzssr)-parseFloat(this.tableData.pageData.jmssrjjsrsdjmdyhje)-parseFloat(this.tableData.pageData.jgdzcjszjtje);
