            this.tableData.pageData.zjgjyztscjyzndbmftsdse = parseFloat(this.tableData.pageData.bqybsdse)  * parseFloat(this.tableData.pageData.qbfzjgftbl)* parseFloat(this.tableData.pageData.zjgjyztscjyznbmftbl);
          }
          return this.tableData.pageData.zjgjyztscjyzndbmftsdse;
