      },
      mounted: function () {
        this.keyboardSwitchingCell("taxTable","text");// input rbtbid;; text:
        //this.getPdf(this.name,true);
      },
      created:function () {
        this.certificateDictionary = this.getDict(this.jsonUrl+"/static/json/dict/certificateDict.json");//
        this.cityDictionary = this.getDict(this.jsonUrl+"/static/json/dict/cityDict.json");//
        this.initializationData = this.getDict(this.jsonUrl+"/static/json/initializationData.json");//
        //this.getInitializationAndSaveInfo({},this.batchData); // 
        this.inTableVerification();// 
        //this.automaticRecode();
        console.log(this.getUrlParam("type"))

        this.resetData=JSON.parse(JSON.stringify(this.tableData)); //this.templateData
        // if(this.getUrlParam("type")=="branch"){
        //  this.getPdf(this.name,true);
       // }
      }
